<script lang='ts'>
  export let passagers: string = '';
  export let dni: string = '';
  export let destination: string = '';
  export let dateTravel: string = '';
  export let number: string = '';

  let canvas: HTMLCanvasElement;
  let img: any;
  $: if (canvas !== undefined) {
    const ctx = canvas.getContext('2d');
    ctx.fillStyle = '#222';
    ctx.fillRect(0, 0, canvas.width, canvas.height);
    ctx.fillStyle = '#fff';
    ctx.font = '2rem Arial';
    ctx.fillText('Ticket De Viaje', 10, 30);
    ctx.font = '1.2rem Arial';
    ctx.fillText(`Pasajero: ${passagers}`, 10, 70);
    ctx.fillText(`DNI: ${dni}`, 10, 100);
    ctx.fillText(`Destino: ${destination}`, 10, 130);
    ctx.fillText(`Fecha de viaje: ${dateTravel}`, 10, 160);
    ctx.fillText(`Telefono: ${number}`, 10, 190);

    canvas.toBlob(blob => {
      img = blob;
    }, 'image/png', 1);
  }

  $: if (img !== undefined) $imgBlob = img;
</script>

<script context='module'>
  import {writable} from 'svelte/store';
  export const imgBlob = writable('');
</script>

<canvas bind:this={canvas} width='350' height='210'></canvas>

<style>
  canvas {
    position: absolute;
    display: none;
  }
</style>
