<script>
  import Ticket from './Ticket.svelte';
  import InputReserve from './InputReserve.svelte';
  import {sendInfo, errors} from './sendForm.js';
  import {AsYouType} from 'libphonenumber-js';
  let number;
  const formatterNumber = (e) => {
    number = new AsYouType('AR').input(e.target.value);
  };

  let name;
  let lastname;
  let dni;
  let destination;
  let dateTravel;
  let cant = '1';
</script>

<form on:submit={sendInfo}>
  <label for='destiny'>DONDE Y CUANDO VIAJAN?</label>
  <InputReserve name='destiny' place='DESTINO' err={$errors.destination} on:keyup={e => destination = e.target.value}/>
  <InputReserve name='dateTravel' place='FECHA DE VIAJE' err={$errors.dateTravel} on:keyup={e => dateTravel = e.target.value}/>
  <label for='pasajeros'>CUANTOS PASAJEROS</label>
  <InputReserve type='number' name='pasajeros' place='SELECCIONE LOS PASAJEROS' err={$errors.cantity} on:keyup={e => cant = e.target.value}/>
  <label for='datosPersonales'>DATOS PERSONALES (TAL CUAL FIGURE EN EL DOCUMENTO DE VIAJE)</label>
  <InputReserve name='name' place='NOMBRE' err={$errors.lastName} on:keyup={e => name = e.target.value}/>
  <InputReserve name='lastName' place='APELLIDO' err={$errors.lastName} on:keyup={e => lastname = e.target.value}/>
  <InputReserve type='number' name='dni' place='DNI' err={$errors.dni} on:keyup={e => dni = e.target.value}/>
  <InputReserve name='nacionality' place='NACIONALIDAD' err={$errors.nacionality}/>
  <InputReserve name='dateTime' place='FECHA DE NACIMIENTO' err={$errors.dateBirth}/>
  <label for='contact'>DATOS DE CONTACTO</label>
  <InputReserve type='email' name='email' place='E-MAIL' err={$errors.email}/>
  <InputReserve name='number' place='TELEFONO' num={number} on:keyup={formatterNumber} err={$errors.phone}/>
  <button>ENVIAR FORMULARIO</button>
</form>
<Ticket passagers='{name} {lastname}' {dni} {number} {destination} {dateTravel}/>

