  form {
    width: 90%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: .4em;
  }

  form label {
    font-family: 'Oswald', sans-serif;
    font-weight: 300;
    font-size: 1.35rem;
    grid-column: 1 / 3;
  }

  form button {
    background-color: #F20083;
    color: #fff;
    font-family: 'Oswald', sans-serif;
    font-weight: 300;
    font-size: 1.1rem;
    grid-column: 2/3;
    width: 60%;
    justify-self: flex-end;
    margin-right: 3.4em;
  }

  @media (max-width: 760px) {
    form {
      grid-template-columns: 1fr;
    }

    form label {
      font-size: 1.25rem;
      grid-column: 1/1;
    }

    form button {
      grid-column: 1/1;
      margin-right: 0;
      justify-self: center;
    }
  }
</style>
