import {isValidPhoneNumber} from 'libphonenumber-js';
import { writable } from 'svelte/store';
import {imgBlob} from './Ticket.svelte';
let imgData;
imgBlob.subscribe(e => imgData = e);

const sendInfo = async (e) => {
	e.preventDefault();
	const values = [];
	const form = new FormData(e.target);
	e.target.childNodes.forEach((item) => {
		if (item.nodeName === "DIV") {
			item.childNodes.forEach((i) => {
				if (i.nodeName === "INPUT") {
					values.push(i.value);
				}
			})
		}
	});
	if (
	validateDestination(values[0]) && validateDate(values[1])
	&& validateCantity(values[2]) && validateName(values[3])
	&& validateName(values[4]) && validateDocument(values[5])
	&& validateNacionality(values[6]) && validateDateOfBirth(values[7])
	&& validateEmail(values[8]) && validatePhone(values[9])) {
		form.append('blob', imgData);
		fetch('https://data-message.deno.dev/data/url', {
			method: 'POST',
			body: form,
		})
	}
}

//Errors of inputs
let errors = writable({
	destination: '',
	dateTravel: '',
	cantity: '',
	lastName: '',
	dni: '',
	nacionality: '',
	dateBirth: '',
	email: '',
	phone: ''
})
//Validation Destination
const validateDestination = (e) => {
	const regex = /^[a-z\sA-Z]*$/;
	if (e.length > 0 && regex.test(e)) {
		errors.update(val => {val.destination = ''; return val});
		return true;
	};
	return errors.update(val => {val.destination = 'error'; return val});
}

//Validation Date Travel
const validateDate = (e) => {
	const regex = /^(?:3[01]|[12][0-9]|0?[1-9])([\-/.])(0?[1-9]|1[1-2])\1\d{4}$/;
	if (e.length > 0 && regex.test(e)) {
		errors.update(val => {val.dateTravel=''; return val});
		return true;
	};
	return errors.update(val => {val.dateTravel='error'; return val});
}

//Validation Cantity of people
const validateCantity = e => {
	const regex = /^[0-9]{1}$/;
	if (e.length > 0 && regex.test(e)) {
		errors.update(val => {val.cantity=''; return val});
		return true;
	};
	return errors.update(val => {val.cantity='error'; return val});
}

//Validation name
const validateName = e => {
	const regex = /^[a-z\sA-Z]*$/;
	if (e.length > 0 && regex.test(e)) {
		errors.update(val => {val.lastName=''; return val});
		return true;
	};
	return errors.update(val => {val.lastName='error'; return val});
}

//Validation document number
const validateDocument = e => {
	const regex = /^[0-9]{8}$/;
	if (e.length < 99999999 && regex.test(e)) {
		errors.update(val => {val.dni=''; return val});
		return true;
	};
	return errors.update(val => {val.dni='error'; return val});
}

//Validation nacionality
const validateNacionality = e => {
	const regex = /^[a-z\sA-Z]*$/;
	if (e.length > 0 && regex.test(e)) {
		errors.update(val => {val.nacionality=''; return val});
		return true;
	};
	return errors.update(val => {val.nacionality='error'; return val});
}

//Validation Date of Birth
const validateDateOfBirth = e => {
	const regex = /^(?:3[01]|[12][0-9]|0?[1-9])([\-/.])(0?[1-9]|1[1-2])\1\d{4}$/;
	if ( e.length > 0 && regex.test(e)) {
		errors.update(val => {val.dateBirth=''; return val});
		return true;
	};
	return errors.update(val => {val.dateBirth='error'; return val});
}

//Validation Email
const validateEmail = e => {
	const regex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
	if (e.length > 0 && regex.test(e)) {
		errors.update(val => {val.email=''; return val});
		return true;
	};
	return errors.update(val => {val.email='error'; return val});
}

//Validation Phone
const validatePhone = e => {
	if (isValidPhoneNumber(e, 'AR')) {
		errors.update(val => {val.phone=''; return val});
		return true;
	};
	return errors.update(val => {val.phone='error'; return val});
}

export {
	sendInfo,
	validateDestination,
	errors,
}
